import * as React from 'react';
import PropTypes from 'prop-types';
import isEqual from 'react-fast-compare';
import cls from 'classnames';
import { chineseAmount } from '../utils';
class ChineseAmount extends React.Component {
    constructor(props) {
        super(props);
        const { amount } = props;
        this.state = {
            amount,
        };
    }
    componentDidUpdate(prevProps) {
        if (!isEqual(prevProps, this.props)) {
            const { amount } = this.props;
            this.setState({
                amount,
            });
        }
    }
    render() {
        const { amount } = this.state;
        const { style = {}, className = '' } = this.props;
        return (<span className={cls('seid-chinese-amount', className)} style={style}>
        {chineseAmount(amount)}
      </span>);
    }
}
ChineseAmount.propTypes = {
    amount: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    className: PropTypes.string,
    style: PropTypes.object,
};
export default ChineseAmount;
